@use '../../scss/' as *;
.parent-element {
    display: flex;
    flex-wrap: wrap;
  }
  
  .tf-work {
    background: var(--primary-color8);
    padding: 20px 29px 34px;
    margin-bottom: 30px;
    position: relative;
    @include transition3;
    display: flex;
    flex-direction: column;
    &::before {
      opacity: 0;
      @include transition5;
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      top: 0px;
      left: 0px;
      border-left: 5px solid #AD0ADA;
      border-top: 5px solid #AD0ADA;
    }
    &::after {
      opacity: 0;
      @include transition5;
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      bottom: 0px;
      right: 0px;
      border-right: 5px solid #AD0ADA;
      border-bottom: 5px solid #AD0ADA;
    }
    .image {
      margin-bottom: 15px;
    }
    .title {
      color: var(--primary-color3);
      margin-bottom: 12px;
    }
    .content {
      font-size: 18px;
      line-height: 28px;
      color: var(--primary-color2);
    }
    &.active,
    &:hover {
      cursor: pointer;
      box-shadow: 0px 5px 15px var(--primary-color36);
      &::before,
      &::after {
        opacity: 1;
        width: 50px;
        height: 50px;
      }
    }
    &.style-2 {
      text-align: center;
      padding: 13px 15px 27px;
      .image {
        margin-bottom: 37px;
      }
      .step {
        color: var(--primary-color5);
      }
      .title {
        margin-bottom: 0;
      }
      &.mb30 {
        padding-bottom: 34px;
        .image {
          margin-bottom: 30px;
        }
      }
    }
  }
  
  